<template>
  <div id="loading-wrapper" v-if="!isloaded">
    <div id="loading-text">Cargardo</div>
    <div id="loading-content"></div>
  </div>
</template>

<script>
export default {
  data: () => {
    return {
      isloaded: false,
    };
  },
  mounted() {
    document.onreadystatechange = () => {
      if (document.readyState == "complete") {
        this.isloaded = true;
      }
    };
  },
};
</script>

